.staff-information{
    width: 90%;
    transition: 0.3s ease-out;
    font-size: 4VW;
    text-align: left;
    margin-top: 10px;
    margin-bottom: 20px;
    margin-right: auto;
    margin-left: auto;
}

.staff-information .passage{
    font-size: 1.6VW;
    text-align: left;
    padding-left: 1.6VW;
    margin-bottom: 5px;
    
}

.staff-information .title{
    font-size: 2.4VW;
    text-align: left;
}

@media screen and (max-width: 780px) {
    .staff-information .passage{
        font-size: 2.4VW;
        padding-left: 2.4VW;
    }
    
    .staff-information .title{
        font-size: 3.2VW;
    }
}